	module.exports = {"data":{"markdownRemark":{"html":"<p>Lの家に行く。彼はB市で訴訟を起こしたと言いましたが、北京の友人に紹介され、そこの女性を知っていたという状況でした。女性は彼女が市政局の司法局であると言って、彼は1400万元以上の価値がある2020年のビッグブラザーの事業について語った。彼は信頼しなかった、そこに行って、司法長官と市長に会った。これは出荷されます。しかしそこには支払いはありませんでした。その結果、司法局は正式な従業員ではなく、女性が雇用されたことを認めなかった。この問題は長い時間がかかり、うまく処理されませんでした。今回は、上海市公安局が来て、「相談通知」を出しました。結果は錦州の女性をつかむことでした。公安局は彼に知らせた。私は最終結果がどうなるか分からない。これは、中国の経済発展と障害の反映における新たな問題である。疑問は、司法局が国家機関としてこのようなことにどのように関与することができるかです。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0122.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0122.md","frontmatter":{"title":"7月17日 日曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0122.md absPath of file >>> MarkdownRemark"}}

